 * @Description: In User Settings Edit
 * @Author: your name
 * @Date: 2019-08-14 14:29:16
 * @LastEditTime: 2019-08-26 09:32:48
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div class="regist_wrapper">
    <Header></Header>

    <el-row type="flex" justify="center" align="middle" class="regist_content">
      <el-col :xs="4" :sm="6" :md="8" :lg="9" :xl="2" class="regist_page box">
        <div class="title">注册账号</div>
        <el-form :model="registInfo" :rules="rules" ref="registInfo" >
          <el-form-item prop="email">
            <el-input v-model="registInfo.email" placeholder="邮箱账号"></el-input>
          </el-form-item>
          <el-form-item prop="emailCode">
            <el-input v-model="registInfo.emailCode" placeholder="6位验证码">
              <el-button :disabled="codes.status?true:false" slot="append" @click.native="getCode" :class="codes.status?'stop':'normal'">{{codes.text}}</el-button>
            </el-input>
          </el-form-item>
          <el-form-item prop="pwd">
            <el-input v-model="registInfo.pwd" type="password" placeholder="设置密码（8-20位数字字母组合）"></el-input>
          </el-form-item>
          <el-form-item prop="confirmPwd">
            <el-input v-model="registInfo.confirmPwd" type='password' placeholder="确认密码"></el-input>
          </el-form-item>
          <el-form-item class="judge_content" >
            <p @click="isInvite = !isInvite" class="judgeIsHas">是否有邀请码? <i :class="isInvite?'el-icon-arrow-right':'el-icon-arrow-down'"></i></p>
          </el-form-item>
          <el-form-item prop="invite" class="invite" v-if="isInvite">
            <el-input v-model="registInfo.invite" placeholder="邀请码（选填）"></el-input>
          </el-form-item>
          <el-form-item class="agree_text" required>
            <el-checkbox v-model="registInfo.check" class="is_agree" >我已阅读并同意<a href="javascript:;">《用户协议》</a></el-checkbox>
          </el-form-item>
          <el-form-item class="regist_btn">
            <el-button type="primary" @click="immRegist" :loading="btnLoad">注册</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>

    <user-foot></user-foot>

  </div>
</template>

<script>
  import Header from '@/components/header/header'
  import UserFoot from '@/components/userFoot/userFoot'
  import { vaildEmail,vaildPassword} from '@/utils/validate'

  export default {
   data() {

    var checkCode = (rule, value, callback) => {
      if(!value){
        this.registInfo.code = true
        return callback(new Error('验证码不能为空'))
      }else if(value.length != 6){
        this.registInfo.code = true
        return callback(new Error('请输入正确格式的验证码'))
      }else{
        this.registInfo.code = false
      }
    }
     
    return {
      registInfo: {
        email: '',
        emailCode: '',
        pwd:'',
        confirmPwd:'',
        invite:'',
        check:false,
      },
      rules:{
        email: [
          { required:true,message: '邮箱不能为空', trigger: 'blur' }
        ],
        emailCode: [
          { validator:checkCode, trigger: 'blur' }
        ],
        pwd: [
          { required:true,message: '密码不能为空',trigger: 'blur' }
        ],
        confirmPwd: [
          { required:true,message: '确认密码不能为空', trigger: 'blur' }
        ]
      },

      codes:{
        text:'获取验证码',
        status:false,
        time:60
      },
      
      isInvite:false,
      btnLoad:false
     }
   },
   created(){
    this.analyAddress()
   },
   methods: {
     /**
      * 获取验证码
      */
     getCode(){
        if(!this.codes.status){

          if(!vaildEmail(this.registInfo.email)){
            this.$message.info('请输入正确格式的邮箱')
            return false
          }
          var dataInfo = {"email":this.registInfo.email}
          this.$store.dispatch('get_email_code',dataInfo).then(res => {
            if(res.data.result == 0){
              let _this = this;
              setTimeout(()=>{
                _this.codes.status = true
                _this.codes.text = _this.codes.time + 's 后重新发送'
              }, 800)
              
              var interval = window.setInterval(()=>{
                _this.codes.text = _this.codes.time + 's 后重新发送';
                --_this.codes.time
                if(_this.codes.time < 0){
                  _this.codes.text = '获取验证码'
                  _this.codes.time = 60
                  _this.codes.status = false
                  window.clearInterval(interval)
                }
              },1000)
            }
          }).catch(err => {

          })
        }
     },
     /**
      * 立即注册
      */
     immRegist(){
       if(!this.btnLoad){
          const validata = this.registInfo
          if(!vaildEmail(validata.email)){
            this.$message.info('请输入正确格式的邮箱');
            return false
          }

          if(!vaildPassword(validata.pwd)){
            this.$message.info('请输入正确格式的密码');
            return false
          }
          
          if(!validata.confirmPwd){
            this.$message.info('确认密码不能为空');
            return false
          }else if(validata.confirmPwd !== validata.pwd){
            this.$message.info('两次输入密码不一致');
            return false
          }
          if(!validata.check){
            this.$message.info('请选择同意协议书')
            return false
          }
          const registInfo = {
            "user":validata.email,
            "capicha":validata.emailCode,
            "passwd":validata.pwd,
            "invite_code":validata.invite
          }
          this.btnLoad = true
          this.$store.dispatch('register', registInfo).then(res => {
            if(res.data.result == 0){
              this.btnLoad = false
              this.$message.success('注册成功')
              setTimeout(()=>{
                this.$router.replace('/login')
              },500)
            }else{
              this.btnLoad = false
            }
          }).catch(err => {
            this.btnLoad = false
          })
       }
     },
    /**
     * 跳转页面
     */
    goTo(str){
      this.$router.replace('/'+str)
    },
    /* 解析路由地址 */
    analyAddress(){
      const route = this.$route
      if(route.fullPath.indexOf('?') != -1){
        this.registInfo.email = route.query.account
      }
    }
   },
   components: {
     Header,UserFoot
   }
  }
</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  @import '@/style/base.scss';

  .regist_wrapper{
    height: 100%;
    width: 100%;
    min-width: 1280px;
    padding-top: 47px;
    .regist_content{
      width: 100%;
      height: calc(100% - 180px);
      background-color:$module_bg_color;
      position: relative;
      .regist_page{
        max-width: 480px;
        min-width: 400px;
        width: 80%;
        max-height: 780px;
        border-radius: 5px;
        background-color: $light_font_color;
        padding:30px 50px;
        box-sizing: border-box;
        position: absolute;
        top:50%;
        left: 50%;
        margin-top: -250px;
        margin-left: -190px;
        .title{
          width: 100%;
          height: 40px;
          line-height: 40px;
          font-size: $font20;
          letter-spacing: 2px;
          color: $light_title_color;
          text-align: center;
          margin-bottom: 20px;
        }
        .judge_content{
          margin-bottom: 5px;
          .judgeIsHas{
            line-height: 20px;
            font-size: $base_font;
            color: $noraml_font_color;
            margin-right: 3px;
            font-weight: 600;
            cursor: pointer;
          }
        }
        
        .invite{
          margin-bottom: 5px;
        }
        .agree_text{
          cursor: pointer;
          font-size: $font14;
          .is_agree{
            margin-right: 10px;
            color: $light_title_color;
          }
          a{
            color:$active_font_color;
            font-size: $font14;
            &:hover{
              text-decoration: underline;
            }
          }
        }
        .regist_btn{
          margin-bottom: 10px;
          .el-button{
            width: 100%;
            background-color: $user_btn_bg_btn;
            border-radius: 3px;
            font-size: $font14;
            letter-spacing: 1px;
            margin-top: 10px;
          }
        }
      }
    }
  }
</style>



